/* Import Google Fonts */


/* Global */
body {
  background-color: #f7f7f7;
}

.app {
  text-align: center;
  min-height: 100vh;
}

footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  margin: 10px auto;
}

header {
  /* margin: 20px auto auto; */
  background-color: #1b1c1d;
  padding: 20px;
  /* background-color: whitesmoke; */
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0 5px black;
  border-bottom: 1px solid #444;
}

.buttonContainer {
  padding: 20px;
}

.relative {
  position: relative;
}

.flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

td {
  min-width: 100px;
}

/* Tool Container */
.toolContainer {
  /* max-width: 500px; */
  position: relative;
  display: flex;
  flex-direction: column;
  /* justify-content: space-between; */
  /* width: auto; */
  min-height: 200px;
  margin: 2px 4px;
  background-color: #ffffff;
  box-shadow: 0 1px 2px #ccc;
  padding: 10px;
  border-radius: 6px;
}

/* For Timers on Larger Screens */
.halfContainer {
  width: 300px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  /* max-height: fit-content; */
  margin: 2px;
}

.mini {
  font-size: 0.9rem;
  line-height: normal;
  line-height: initial;
}

.ui.buttons {
  width: 100%;
}

.ui.buttons .button {
  flex: 1 1 auto;
}

.dice {
  font-size: 3rem;
  display: block;
  margin: 0 5px 5px;
}

.diceButtons .button {
  padding: 8px 5px;
}

.diceButtons .dice {
  font-size: 2.5rem;
}

.diceContainer {
  min-height: 50px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 9px;
}

/* Add User Object Button Icon
Add Score Type Button Icon */

/* Delete Butotns */
.delete {
  position: absolute;
  color: #011627;
  background: none;
  border: none;
  top: 8px;
  right: 8px;
  /* z-index: 10; */
  cursor: pointer;
  width: auto;
  width: initial;
  padding: 0 5px;
}

.delete:hover {
  color: grey;
}

/* Timer/Countdown Tool Timer Display */
.timeDisplay {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: flex-end;
  margin: 0 auto 20px;
  padding: 5px 7px;
  border-radius: 5px;
  font-family: 'Inconsolata', monospace;
  font-size: 1.6rem;
  font-weight: bold;
  letter-spacing: 4px;
  line-height: inherit;
  background-color: #011627;
  color: #f6f7f8;
}

/* Media Queries */
/* @media (max-width: 1300px){

} */

/* @media (max-width: 900px) {
  
} */

@media (max-width: 660px) {
  header {
    padding: 0;
  }

  .toolContainer {
    margin: 5px;
  }
}


@media (max-width: 460px) {
  
  .timeDisplay {
    width: 100%;
  }

}


